.footer {
  position: absolute;
  bottom: 0;
  left: 50%;
  -webkit-transform: translate3d(-50%, 50%, 0);
  transform: translate3d(-50%, 50%, 0);
}
.footer .icon {
  width: 20px;
  height: 20px;
  vertical-align: bottom;
}
.footer a {
  cursor: pointer;
  margin-left: 10px;
}



